import { css } from 'emotion';
import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { ExpandCollapseAllContainer } from './expand-collapse-all-container'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { FolderList } from './folder-list'; // eslint-disable-line @typescript-eslint/no-unused-vars
import * as Context from '../../context';
import * as Folder from '../../core/model/folder';
import * as State from '../../core/model/state';

export interface FolderListContainerInjectedProps {
    ids: Folder.Id[];
    viewType: string;
}

export function mapStateToProps(state: State.Type): FolderListContainerInjectedProps {
    let ids: Folder.Id[] = [];
    if (State.applicationViewType(state) === 'folders') {
        ids = State.folderOrder(state) as Folder.Id[];
    }
    if (State.applicationViewType(state) === 'alphabetical') {
        ids = State.letterOrder(state) as Folder.Id[];
    }
    return {
        ids,
        viewType: State.applicationViewType(state)
    };
}

const className = css({
    display: 'flex',
    flexDirection: 'column',
    height: '100%'
});

export const PureFolderListContainer: FunctionComponent<FolderListContainerInjectedProps> = ({ ids, viewType }) => (
    <div data-testid="app-menu__by-folder" className={className}>
        <ExpandCollapseAllContainer />
        <FolderList ids={ids} viewType={viewType} />
    </div>
);

export const FolderListContainer = Context.connect(PureFolderListContainer, mapStateToProps);
