import * as State from './core/model/state';
import * as Folder from './core/model/folder';

export interface Type {
    folder__added(state: State.Type, folders: Folder.Type[]): void;
    folder__closed(state: State.Type, id: Folder.Id): void;
    folder__opened(state: State.Type, id: Folder.Id): void;
    folder__allOpened(state: State.Type): void;
    folder__allClosed(state: State.Type): void;
    letter__added(state: State.Type, letters: Folder.Type[]): void;
    letter__closed(state: State.Type, id: Folder.Id): void;
    letter__opened(state: State.Type, id: Folder.Id): void;
    letter__allOpened(state: State.Type): void;
    letter__allClosed(state: State.Type): void;
    menu__closed(state: State.Type): void;
    menu__opened(state: State.Type): void;
    view__toggled(state: State.Type): void;
}

export function of(): Type {
    return {
        folder__added: State.addFolders,
        folder__closed: State.closeFolder,
        folder__opened: State.openFolder,
        folder__allOpened: State.openAllFolders,
        folder__allClosed: State.closeAllFolders,
        letter__added: State.addLetters,
        letter__closed: State.closeLetter,
        letter__opened: State.openLetter,
        letter__allOpened: State.openAllLetters,
        letter__allClosed: State.closeAllLetters,
        menu__closed: State.close,
        menu__opened: State.open,
        view__toggled: State.toggleView
    };
}
