/* eslint-disable no-shadow */

import * as SubApp from './sub-app';

export interface Type {
    readonly id: string;
    readonly href: string;
    readonly labelKey: string;
    readonly label: string;
    readonly subApps: SubApp.Type[];
    readonly subscript?: string;
}

export function of(
    id: string,
    href: string,
    labelKey: string,
    label: string,
    subApps: SubApp.Type[],
    subscript?: string
): Type {
    return Object.freeze({ id, href, labelKey, label, subApps, subscript });
}

export function id(app: Type): string {
    return app.id;
}

export function href(app: Type): string {
    return app.href;
}

export function labelkey(app: Type): string {
    return app.labelKey;
}

export function label(app: Type): string {
    return app.label;
}

export function subApps(app: Type): SubApp.Type[] {
    return app.subApps;
}

export function subscript(app: Type): string {
    return app.subscript || '';
}
