/* eslint-disable no-shadow */

export interface Type {
    readonly id: string;
    readonly href: string;
    readonly labelKey: string;
    readonly label: string;
    readonly subscript?: string;
}

export function of(id: string, href: string, labelKey: string, label: string, subscript?: string): Type {
    return Object.freeze({ id, href, labelKey, label, subscript });
}

export function id(subApp: Type): string {
    return subApp.id;
}

export function href(subApp: Type): string {
    return subApp.href;
}

export function labelKey(subApp: Type): string {
    return subApp.labelKey;
}

export function label(subApp: Type): string {
    return subApp.label;
}

export function subscript(subApp: Type): string {
    return subApp.subscript || '';
}
