import { render, FunctionComponent } from 'preact';
import { StatelessDucklet, Update } from '@ix/guiduck';
import { Nullable } from '../type-dictionary';

export class StatelessPreactDucklet<Tattrs> implements StatelessDucklet<Tattrs> {
    private element: Nullable<HTMLElement> = null;

    private functionComponent: FunctionComponent<Tattrs>;

    constructor(functionComponent: FunctionComponent<Tattrs>) {
        this.functionComponent = functionComponent;
    }

    mount(element: HTMLElement, attrs?: Tattrs): this {
        if (this.element) throw new Error('Component is already mounted');
        this.element = element;
        if (attrs) this.update(() => attrs);
        return this;
    }

    unmount(): HTMLElement {
        if (!this.element) throw new Error('Component has not been mounted');
        this.unmountPreact();
        this.deleteContents();
        const { element } = this;
        this.element = null;
        return element;
    }

    private unmountPreact(): void {
        render(null, this.element as Element);
    }

    private deleteContents(): void {
        while (this.element?.firstChild) {
            this.element.removeChild(this.element.firstChild);
        }
    }

    update(callback: Update<Tattrs, undefined>): this {
        if (!this.element) throw new Error('Component has not been mounted');
        const attrs = callback();
        const virtualDom = this.functionComponent(attrs as any);
        render(virtualDom, this.element);
        return this;
    }
}
