import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css } from 'emotion';
import {
    centerHorizontallyVertically,
    clickableCursor,
    gray1,
    gray18,
    primaryHover,
    primaryPressed
} from '../../../design-system';

export interface ToggleProps {
    onClick: () => void;
    tooltip: string;
}

const className = css({
    ...centerHorizontallyVertically(),
    ...clickableCursor(),
    backgroundColor: gray1,
    border: 'none',
    height: '40px',
    width: '46px',

    i: {
        color: gray18,
        fontSize: '16px'
    },
    ':hover': {
        i: {
            color: primaryHover
        }
    },
    ':active': {
        i: {
            color: primaryPressed
        }
    }
});

export const Toggle: FunctionComponent<ToggleProps> = ({ onClick, tooltip }) => (
    <div data-testid="app-menu__toggle" className={className} onClick={onClick} title={tooltip}>
        <i class="gedi-mobile-menu" />
    </div>
);
