import createStore, * as unistore from 'unistore';
import * as State from './core/model/state';

export type Type = unistore.Store<State.Type>;

export type StateMapper<PassedInProps, PropsToInject> = unistore.StateMapper<PassedInProps, State.Type, PropsToInject>;

export function of(state: State.Type): Type {
    return createStore(state);
}
