import { css } from 'emotion';
import { Ducklet } from '@ix/guiduck';
import { ImmutableDuckletList } from '../ui-primitives';
import { gray1, sensibleBoxModel } from '../design-system';

export type Attrs = ImmutableDuckletList.Attrs;

export type Type = ImmutableDuckletList.Type;

const statusBarClassName = css({
    ...sensibleBoxModel(),
    backgroundColor: gray1,
    display: 'flex',
    height: '24px',
    justifyContent: 'space-between',
    padding: '0 15px 0 15px',

    li: {
        display: 'inline-flex'
    }
});

const childListClassName = css({
    display: 'inline-flex',

    li: {
        alignItems: 'center',
        display: 'inline-flex'
    }
});

export function of(leftDucklets: Ducklet<any, any>[], rightDucklets: Ducklet<any, any>[]): Type {
    const left = childList('status-bar__left', leftDucklets);
    const right = childList('status-bar__right', rightDucklets);
    return ImmutableDuckletList.of([left, right], statusBarClassName, { testid: 'status-bar' });
}

function childList(testid: string, ducklets: Ducklet<any, any>[]): ImmutableDuckletList.Type {
    return ImmutableDuckletList.of(ducklets, childListClassName, { testid });
}
