import { h } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { StatelessDucklet } from '@ix/guiduck';
import { CommonHelpProps } from './common-help-props';
import { HelpOnPrem, HelpOnPremProps } from './help-on-prem'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { HelpPE } from './help-pe'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { HelpPEMobile, HelpPEMobileProps } from './help-pe-mobile'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { HelpUnified } from './help-unified'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { HelpUnifiedMobile, HelpUnifiedMobileProps } from './help-unified-mobile'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { StatelessPreactDucklet } from '../guiduck-preact';
import { PreexistingAttrDecorator } from '../ui-primitives';

export type Attrs = HelpOnPremProps & HelpUnifiedMobileProps & HelpPEMobileProps & CommonHelpProps;

export type Type = StatelessDucklet<Attrs>;

export enum Platform {
    OnPrem = 'on-prem',
    Unified = 'unified',
    UnifiedMobile = 'unified-mobile',
    PredixEssentials = 'pe',
    PredixEssentialsMobile = 'pe-mobile'
}

const factory = (type: string) => {
    if (type === Platform.OnPrem) {
        return (attrs: HelpOnPremProps) => (
            <HelpOnPrem
                icon={attrs.icon}
                tooltip={attrs.tooltip}
                testid={attrs.testid}
                onClick={attrs.onClick}
                baseUrl={attrs.baseUrl}
                context={attrs.context}
                searchTerm={attrs.searchTerm}
                culture={attrs.culture}
            />
        );
    }

    if (type === Platform.Unified) {
        return (attrs: CommonHelpProps) => (
            <HelpUnified
                icon={attrs.icon}
                tooltip={attrs.tooltip}
                testid={attrs.testid}
                onClick={attrs.onClick}
                searchTerm={attrs.searchTerm}
            />
        );
    }

    if (type === Platform.UnifiedMobile) {
        return (attrs: HelpUnifiedMobileProps) => (
            <HelpUnifiedMobile
                icon={attrs.icon}
                tooltip={attrs.tooltip}
                testid={attrs.testid}
                onClick={attrs.onClick}
                baseUrl={attrs.baseUrl}
                context={attrs.context}
                searchTerm={attrs.searchTerm}
                culture={attrs.culture}
            />
        );
    }

    if (type === Platform.PredixEssentials) {
        return (attrs: CommonHelpProps) => (
            <HelpPE
                icon={attrs.icon}
                tooltip={attrs.tooltip}
                testid={attrs.testid}
                onClick={attrs.onClick}
                searchTerm={attrs.searchTerm}
            />
        );
    }

    if (type === Platform.PredixEssentialsMobile) {
        return (attrs: HelpPEMobileProps) => (
            <HelpPEMobile
                icon={attrs.icon}
                tooltip={attrs.tooltip}
                testid={attrs.testid}
                onClick={attrs.onClick}
                baseUrl={attrs.baseUrl}
                context={attrs.context}
                searchTerm={attrs.searchTerm}
                culture={attrs.culture}
            />
        );
    }

    throw new Error(`No help button type found for type ${type}`);
};

class HelpButton extends StatelessPreactDucklet<Attrs> implements Type {
    constructor(type: string) {
        super(factory(type));
    }
}

export function of(type: string, attrs: Partial<Attrs> = {}): Type {
    return PreexistingAttrDecorator.of(new HelpButton(type), attrs);
}
