import * as SubApp from '../../core/model/sub-app';
import * as Folder from '../../core/model/folder';
import { ApplicationProps } from './application';

export function mapSubAppProps(folder: Folder.Type, subApp: SubApp.Type): ApplicationProps {
    return {
        label: SubApp.label(subApp),
        labelKey: SubApp.labelKey(subApp),
        folderLabel: Folder.label(folder),
        folderKey: Folder.labelKey(folder),
        tooltip: SubApp.label(subApp),
        href: SubApp.href(subApp),
        target: 'tab'
    };
}
