import { h } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { StatelessDucklet } from '@ix/guiduck';
import { Dropdown, DropdownProps, PreexistingAttrDecorator } from './ui-primitives'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { StatelessPreactDucklet } from './guiduck-preact';

type Attrs = DropdownProps;

export type Type = StatelessDucklet<Attrs>;

class UtilityMenuDropdown extends StatelessPreactDucklet<Attrs> implements Type {
    constructor() {
        super((attrs: Attrs) => {
            return (
                <Dropdown
                    icon={attrs.icon}
                    tooltip={attrs.tooltip}
                    children={attrs.children}
                    testid={attrs.testid}
                    dropdownTestId={attrs.dropdownTestId}
                    expanded={attrs.expanded}
                    onClick={attrs.onClick}
                />
            );
        });
    }
}

export function of(attrs: Partial<Attrs> = {}) {
    return PreexistingAttrDecorator.of(new UtilityMenuDropdown(), attrs);
}
