import { css, cx } from 'emotion';
import { TransparentButton } from '../common';

const defaultClassName = css({
    height: '32px',
    lineHeight: 0,
    margin: 0,
    padding: 0,
    width: '32px',

    i: {
        fontSize: '16px'
    }
});

export function className(): string {
    return cx(TransparentButton, defaultClassName);
}
