import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { optionalProp, requiredProp } from '../preact-common';
import { className } from './class-name';
import { CommonHelpProps } from './common-help-props';

const UNIFIED_DEFAULT_HELP_PAGE = 'https://www.ge.com/digital/documentation/predix-apm/';
const SEARCH_SUFFIX = 'search.html?searchQuery=';

const buildSearchUrl = (props: CommonHelpProps) => {
    return UNIFIED_DEFAULT_HELP_PAGE + SEARCH_SUFFIX + props.searchTerm;
};

const format = (props: CommonHelpProps) => {
    if (props.searchTerm) {
        return buildSearchUrl(props);
    }
    return UNIFIED_DEFAULT_HELP_PAGE;
};

const testid = (props: CommonHelpProps) => optionalProp<string>('data-testid', props.testid);

const onClickWrapper = (props: CommonHelpProps) => {
    props.onClick(format(props));
};

const tooltip = (props: CommonHelpProps) => requiredProp<string>('title', props.tooltip, 'tooltip');

const icon = (props: CommonHelpProps) => requiredProp<string>('className', props.icon, 'icon');

const onClick = (props: CommonHelpProps) =>
    requiredProp<(ev: MouseEvent) => void>('onClick', onClickWrapper.bind(null, props));

export const HelpUnified: FunctionComponent<CommonHelpProps> = (props: CommonHelpProps) => {
    return (
        <div>
            <button className={className()} {...{ ...onClick(props), ...testid(props), ...tooltip(props) }}>
                <i {...{ ...icon(props) }} />
            </button>
        </div>
    );
};
