import { css, cx } from 'emotion';
import { Ducklet, StatelessDucklet, Update } from '@ix/guiduck';
import { MountPoint } from '../guiduck';
import * as PreexistingAttrDecorator from './preexisting-attr-decorator';

export type Attrs = {
    className: string;
    testid: string;
};

export type Type = StatelessDucklet<Attrs>;

const defaultAttrs: Attrs = {
    className: '',
    testid: ''
};

const defaultClassName = css({
    boxSizing: 'border-box',
    lineHeight: 0,
    listStyleType: 'none',
    margin: 0,
    padding: 0,

    li: {
        margin: 0,
        padding: 0
    }
});

class ImmutableDuckletList implements Type {
    private readonly children: Ducklet<any, any>[];

    private readonly mountPoint = new MountPoint();

    private readonly immutableClassName: string;

    constructor(children: Ducklet<any, any>[], immutableClassName: string) {
        this.children = children;
        this.immutableClassName = immutableClassName;
    }

    mount(element: HTMLElement, attrs?: Partial<Attrs>): this {
        this.mountPoint.mount(element, 'ul');
        this.applyAttrs(attrs);
        this.mountChildren();
        return this;
    }

    private applyAttrs(attrs?: Partial<Attrs>) {
        this.setClassNames(attrs);
        this.setTestId(attrs);
    }

    private setClassNames(attrs?: Partial<Attrs>): void {
        this.mountPoint.withFirstChild(firstChild => {
            const { className } = { ...defaultAttrs, ...attrs };
            firstChild.className = cx(defaultClassName, this.immutableClassName, className);
        });
    }

    private setTestId(attrs?: Partial<Attrs>): void {
        this.mountPoint.withFirstChild(firstChild => {
            const { testid } = { ...defaultAttrs, ...attrs };
            if (testid.length) firstChild.setAttribute('data-testid', testid);
        });
    }

    private mountChildren(): void {
        this.children.forEach(child => this.mountChild(child));
    }

    private mountChild(child: Ducklet<any, any>): void {
        this.mountPoint.withFirstChild(firstChild => {
            const childMountPoint = document.createElement('li');
            firstChild.appendChild(childMountPoint);
            child.mount(childMountPoint);
        });
    }

    unmount(): HTMLElement {
        this.unmountChildren();
        return this.mountPoint.unmount();
    }

    private unmountChildren(): void {
        this.children.forEach(child => child.unmount());
    }

    update(callback: Update<Attrs, undefined>): this {
        const attrs = callback();
        this.applyAttrs(attrs);
        return this;
    }
}

export function of(children: Ducklet<any, any>[], immutableClassName = '', attrs: Partial<Attrs> = {}): Type {
    return PreexistingAttrDecorator.of(new ImmutableDuckletList(children, immutableClassName), attrs);
}

export function ofEmpty(): Type {
    return of([]);
}
