import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { optionalProp, requiredProp } from '../preact-common';
import { formatContextForRedirect } from './utils/format-context-for-redirect';
import { className } from './class-name';
import { CommonHelpProps } from './common-help-props';

export interface HelpOnPremProps extends CommonHelpProps {
    culture: string;
    baseUrl: string;
    context?: string;
}

const ON_PREM_DEFAULT_HELP_PAGE = 'default.html';
const ON_PREM_SEARCH_URL = 'search.html?searchQuery=';

const getUrlPrefix = (props: HelpOnPremProps) => {
    return `${props.baseUrl}meridium/Help/${props.culture}/`;
};

const buildDefaultUrl = (props: HelpOnPremProps) => {
    return getUrlPrefix(props) + ON_PREM_DEFAULT_HELP_PAGE;
};

const buildSearchUrl = (props: HelpOnPremProps) => {
    return getUrlPrefix(props) + ON_PREM_SEARCH_URL + props.searchTerm;
};

const buildContextUrl = (props: HelpOnPremProps) => {
    return getUrlPrefix(props) + formatContextForRedirect(props.context);
};

const format = (props: HelpOnPremProps) => {
    if (props.searchTerm) {
        return buildSearchUrl(props);
    }
    if (props.context) {
        return buildContextUrl(props);
    }
    return buildDefaultUrl(props);
};

const testid = (props: HelpOnPremProps) => optionalProp<string>('data-testid', props.testid);

const onClickWrapper = (props: HelpOnPremProps) => {
    props.onClick(format(props));
};

const tooltip = (props: HelpOnPremProps) => requiredProp<string>('title', props.tooltip, 'tooltip');

const icon = (props: HelpOnPremProps) => requiredProp<string>('className', props.icon, 'icon');

const onClick = (props: HelpOnPremProps) =>
    requiredProp<(ev: MouseEvent) => void>('onClick', onClickWrapper.bind(null, props));

export const HelpOnPrem: FunctionComponent<HelpOnPremProps> = (props: HelpOnPremProps) => {
    return (
        <div>
            <button className={className()} {...{ ...onClick(props), ...testid(props), ...tooltip(props) }}>
                <i {...{ ...icon(props) }} />
            </button>
        </div>
    );
};
