import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css, cx } from 'emotion';
import { toggle } from '@ix/design-tokens/lib/toggle';
import { optionalProp, requiredProp, StatelessEvent } from '../preact-common';
import { bem, guid, stopPropagation } from '../common';

export interface ToggleButtonProps {
    onClick: (ev: ToggleButtonClickEvent) => void;
    testid?: string;
    tooltip: string;
    value?: boolean;
    disabled?: boolean;
    className?: string;
}

export type ToggleButtonClickEvent = StatelessEvent<MouseEvent, ToggleButtonProps, { value: boolean | undefined }>;

const slot = '::before';
const slotSelectedDisabled = `:disabled > :checked + ${slot}`;
const slotSelectedHover = `:hover:not([disabled]) > :checked + ${slot}`;
const slotSelectedNormal = `:checked + ${slot}`;
const slotSelectedPressed = `:active:not([disabled]) > :checked + ${slot}`;
const slotUnselectedDisabled = `:disabled > ${slot}`;
const slotUnselectedHover = `:hover:not([disabled]) > ${slot}`;
const slotUnselectedPressed = `:active:not([disabled]) > ${slot}`;

const handle = '::after';
const handleSelectedNormal = `:checked + ${handle}`;
const handleSelectedPressed = `:active:not([disabled]) > ${handle}`;
const handleUnselectedDisabled = `:disabled > ${handle}`;
const handUnselectedHover = `:hover:not([disabled]) > ${handle}`;

const switchLabel = css`
    display: block;
    width: ${toggle.slot.width};
    height: ${toggle.slot.height};
    text-indent: -150%;
    clip: rect(0 0 0 0);
    color: transparent;
    user-select: none;

    ${slot} {
        content: '';
        display: block;
        position: absolute;
        cursor: pointer;

        width: 100%;
        height: ${toggle.slot.height};
        background-color: ${toggle.slot.unselected.normal.bgColor.light};
        border-radius: ${toggle.slot.borderRadius};
        -webkit-transition: background-color ${toggle.transition} ease;
        transition: background-color ${toggle.transition} ease;
        border: ${toggle.slot.unselected.normal.border.light};
    }

    ${handle} {
        content: '';
        display: block;
        position: absolute;
        cursor: pointer;

        top: -2px;
        left: 0;
        width: ${toggle.handle.width};
        height: ${toggle.handle.height};
        border-radius: ${toggle.handle.borderRadius};
        background-color: white;
        box-shadow: ${toggle.handle.unselected.normal.shadow.light};
        -webkit-transition: left ${toggle.transition} ease;
        transition: left ${toggle.transition} ease;
        border: ${toggle.handle.unselected.normal.border.light};
    }
`;

const switchInput = css`
    display: none;
    ${slotSelectedNormal} {
        background-color: ${toggle.slot.selected.normal.bgColor.light};
        border: ${toggle.slot.selected.normal.border.light};
    }
    ${handleSelectedNormal} {
        left: 30px;
    }
`;

const buttonStyle = css`
    position: relative;
    display: inline-block;
    border: none;
    background-color: transparent;

    :focus {
        outline: none;
    }

    ${slotUnselectedDisabled} {
        background-color: ${toggle.slot.unselected.disabled.bgColor.light};
        cursor: not-allowed;
        pointer-events: none;
        border: ${toggle.slot.unselected.disabled.border.light};
    }

    ${slotSelectedDisabled} {
        background-color: ${toggle.slot.selected.disabled.bgColor.light};
        border: ${toggle.slot.selected.disabled.border.light};
        cursor: not-allowed;
        pointer-events: none;
    }

    ${handleUnselectedDisabled} {
        cursor: not-allowed;
        pointer-events: none;
        border: ${toggle.handle.unselected.disabled.border.light};
    }

    ${slotUnselectedHover} {
        background-color: ${toggle.slot.unselected.hover.bgColor.light};
        border: ${toggle.slot.unselected.hover.border.light};
    }

    ${slotSelectedHover} {
        background-color: ${toggle.slot.selected.hover.bgColor.light};
        border: ${toggle.slot.selected.hover.border.light};
    }

    ${slotUnselectedPressed} {
        background-color: ${toggle.slot.unselected.pressed.bgColor.light};
        border: ${toggle.slot.unselected.pressed.border.light};
    }

    ${slotSelectedPressed} {
        background-color: ${toggle.slot.selected.pressed.bgColor.light};
        border: ${toggle.slot.selected.pressed.border.light};
    }

    ${handleSelectedPressed} {
        border: ${toggle.handle.selected.pressed.border.light};
    }

    ${handUnselectedHover} {
        background-color: ${toggle.handle.unselected.hover.bgColor.light};
        border: ${toggle.handle.unselected.hover.border.light};
    }
`;

const onClickWrapper = (props: ToggleButtonProps, event: MouseEvent) => {
    if (!props.disabled) {
        // eslint-disable-next-line no-param-reassign
        props.value = !props.value;
    }
    const customEvent = {
        props,
        value: props.value,
        nativeEvent: event
    };
    props.onClick(customEvent);
};

const tooltip = (props: ToggleButtonProps) => requiredProp<string>('tooltip', props.tooltip);
const value = (props: ToggleButtonProps) => optionalProp('value', props.value);
const testid = (props: ToggleButtonProps) => optionalProp<string>('data-testid', props.testid);
const onClick = (props: ToggleButtonProps) =>
    requiredProp<(ev: MouseEvent) => void>('onClick', onClickWrapper.bind(null, props));
const disabled = (props: ToggleButtonProps) => optionalProp('disabled', props.disabled);
const className = (props: ToggleButtonProps) => cx(buttonStyle, props.className);

export const ToggleButton: FunctionComponent<ToggleButtonProps> = props => {
    const elementId = guid();
    return (
        <button
            className={className(props)}
            {...{
                ...testid(props),
                ...tooltip(props),
                ...value(props),
                ...disabled(props),
                ...onClick(props)
            }}
        >
            <input
                id={elementId}
                type="checkbox"
                disabled={props.disabled}
                checked={props.value}
                className={switchInput}
                data-testid={bem(props.testid, 'value')}
            />
            <label for={elementId} className={switchLabel} onClick={stopPropagation}>
                {props.tooltip}
            </label>
        </button>
    );
};
