import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css, cx } from 'emotion';
import { gray1, gray3, gray18, gray14 } from '../../../design-system';

type SubAppProps = ApplicationProps;
export interface ApplicationProps {
    label: string;
    labelKey?: string;
    folderLabel?: string;
    folderKey?: string;
    tooltip: string;
    href?: string; // optional because empty href strings are used to make an application a "header" for subapps
    subApps?: Array<SubAppProps>;
    target?: string;
    viewType?: string;
    subscript?: string;
}

const ApplicationStyle = css({
    fontSize: '15px',
    margin: 0,
    padding: 0,
    listStyle: 'none',
    cursor: 'pointer',
    fontFamily: 'GE Inspira Sans',
    span: {
        minWidth: '100%',
        color: gray18
    },
    a: {
        ':first-child': {
            paddingLeft: '20px'
        },
        paddingLeft: '40px',
        backgroundColor: gray1,
        display: 'flex',
        height: '30px',
        textDecoration: 'none',
        lineHeight: '30px',
        ':hover': {
            backgroundColor: gray3
        }
    }
});

const SubscriptStyle = css({
    a: {
        height: '30px',
        lineHeight: 'normal',
        paddingTop: '5px',
        paddingBottom: '5px',
        boxSizing: 'content-box',
        span: {
            display: 'table',
            span: {
                fontSize: '12px',
                color: gray14
            }
        }
    }
});

const className = (props: ApplicationProps) => {
    if (props.viewType === 'alphabetical') {
        return cx(ApplicationStyle, SubscriptStyle);
    }
    return ApplicationStyle;
};

const applicationTestId = (props: ApplicationProps) => {
    return `app-menu__app::${props.folderKey || props.folderLabel}/${props.labelKey || props.label}`;
};

const subAppTestId = (props: ApplicationProps, subAppLabel: string, subAppLabelKey?: string) => {
    return `app-menu__subapp::${props.folderKey || props.folderLabel}/${props.labelKey || props.label}/${
        subAppLabelKey || subAppLabel
    }`;
};

const subscript = (props: ApplicationProps) => {
    if (props.viewType === 'alphabetical' && props.subscript) {
        return <span>{props.subscript}</span>;
    }
    return <span></span>;
};

const subApps = (props: ApplicationProps) => {
    if (props.subApps) {
        return props.subApps.map(subApp => (
            <a
                title={subApp.tooltip}
                data-testid={subAppTestId(props, subApp.label, subApp.labelKey)}
                href={subApp.href}
                target={subApp.target}
            >
                <span>{subApp.label}</span>
            </a>
        ));
    }
    return null;
};

export const Application: FunctionComponent<ApplicationProps> = props => (
    <li className={className(props)}>
        <a title={props.tooltip} data-testid={applicationTestId(props)} href={props.href} target={props.target}>
            <span>
                {props.label}
                <span>{subscript(props)}</span>
            </span>
        </a>
        {subApps(props)}
    </li>
);
