import { css } from 'emotion';
import { StatelessDucklet, Update } from '@ix/guiduck';
import { MouseEventHander, Nullable } from '../type-dictionary';
import { MountPoint } from '../guiduck';

export interface IconAttrs {
    height: string;
    icon: string;
    onClick: MouseEventHander;
    size: string;
    testid: string;
    width: string;
}

const defaultAttrs: IconAttrs = {
    height: '0',
    icon: '',
    onClick: () => undefined,
    size: '',
    testid: '',
    width: '0'
};

const defaultClassName = css({
    alignItems: 'center',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'center',
    textAlign: 'center'
});

export class Icon implements StatelessDucklet<IconAttrs> {
    private initialAttrs: IconAttrs;

    private mountPoint = new MountPoint();

    private onClick: Nullable<MouseEventHander> = null;

    constructor(initialAttrs: Partial<IconAttrs> = {}) {
        this.initialAttrs = { ...defaultAttrs, ...initialAttrs };
    }

    mount(element: HTMLElement, attrs?: Partial<IconAttrs>): this {
        this.mountPoint.mount(element, 'div');
        this.applyAttrs({ ...this.initialAttrs, ...attrs });
        return this;
    }

    private applyAttrs(attrs?: Partial<IconAttrs>) {
        const useAttrs = { ...defaultAttrs, ...attrs };
        this.applyStyles(useAttrs);
        this.applyTestid(useAttrs);
        this.applyOnClick(useAttrs);
        this.addIcon(useAttrs);
    }

    private applyStyles(attrs: IconAttrs): void {
        this.mountPoint.withFirstChild(firstChild => {
            firstChild.className = defaultClassName;
            firstChild.style.height = attrs.height;
            firstChild.style.width = attrs.width;
        });
    }

    private applyTestid(attrs: IconAttrs): void {
        this.mountPoint.withFirstChild(firstChild => {
            if (attrs.testid.length) {
                firstChild.setAttribute('data-testid', attrs.testid);
            }
        });
    }

    private applyOnClick(attrs: IconAttrs): void {
        this.mountPoint.withFirstChild(firstChild => {
            if (this.onClick) firstChild.removeEventListener('click', this.onClick);
            this.onClick = attrs.onClick;
            firstChild.addEventListener('click', this.onClick);
        });
    }

    private addIcon(attrs: IconAttrs): void {
        this.mountPoint.withFirstChild(firstChild => {
            let i = firstChild.firstChild as HTMLElement;
            if (!i) {
                i = document.createElement('i');
                firstChild.appendChild(i);
            }
            i.className = attrs.icon;
            i.style.fontSize = attrs.size;
        });
    }

    unmount(): HTMLElement {
        this.clearOnClick();
        return this.mountPoint.unmount();
    }

    private clearOnClick() {
        this.mountPoint.withFirstChild(element => {
            if (this.onClick) element.removeEventListener('click', this.onClick);
        });
        this.onClick = null;
    }

    update(callback: Update<IconAttrs, undefined>): this {
        const attrs = callback();
        this.applyAttrs(attrs);
        return this;
    }
}
