import { Ducklet, Update } from '@ix/guiduck';
import { render } from 'preact';
import { AppDataSource } from '../model/app-data-source';
import { AppMenu } from './app-menu';
import { MountPoint } from '../../../guiduck';
import * as Context from '../../context';
import * as State from '../model/state';

export type Tattrs = {};
export type Tapi = {};
export type Type = Ducklet<Tattrs, Tapi>;

class Impl implements Type {
    private mountPoint = new MountPoint();

    constructor(private datasource: AppDataSource, options: Partial<State.UserInterface>) {
        const state = State.of({}, options);
        Context.of(state);
    }

    mount(element: HTMLElement, attrs: Tattrs = {}): this {
        this.mountPoint.mount(element, 'div');
        this.update(() => attrs);
        this.datasource.initialAppData().then(appData => {
            Context.actions().folder__added(appData.folders);
            Context.actions().letter__added(appData.folders);
        });
        this.closeOnOutsideClick();
        return this;
    }

    private closeOnOutsideClick(): void {
        const { menu__closed } = Context.actions();
        window.addEventListener('click', menu__closed);
        this.mountPoint.withFirstChild(firstChild => {
            firstChild.addEventListener('click', (ev: MouseEvent) => {
                if (!withinAnchor(ev)) {
                    ev.stopPropagation();
                }
            });
        });
    }

    unmount(): HTMLElement {
        this.mountPoint.withFirstChild(child => render(null, child));
        return this.mountPoint.unmount();
    }

    update(callback: Update<Tattrs, Tapi>): this {
        const newAttrs = callback({});
        this.mountPoint.withFirstChild(child => render(AppMenu(newAttrs), child));
        return this;
    }
}

function withinAnchor(ev: MouseEvent): boolean {
    let element: Element | null = ev.target as Element;
    while (element !== null) {
        if (element.tagName === 'A') return true;
        element = element.parentElement;
    }
    return false;
}

export function of(datasource: AppDataSource, options: { title: string }): Type {
    return new Impl(datasource, options);
}
