import { css } from 'emotion';
import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { FolderContainer } from './folder-container'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { verticalAutoScroll, fillFlexContainer } from '../../../design-system';

export interface FolderListProps {
    ids: string[];
    viewType: string;
}

const className = css({
    ...verticalAutoScroll(),
    ...fillFlexContainer(),
    width: '100%'
});

export const FolderList: FunctionComponent<FolderListProps> = ({ ids, viewType }) => (
    <div data-testid="app-menu__folder-list" className={className}>
        {ids.map(id => (
            <FolderContainer id={id} viewType={viewType} />
        ))}
    </div>
);
