import { h, Component } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { createPopper, Instance } from '@popperjs/core';
import { DropdownItemProps } from './dropdown-item';
import { Nullable } from '../type-dictionary';

interface PopperWrapperProps {
    children?: Array<DropdownItemProps>;
    expanded?: boolean;
}
export class PopperWrapper extends Component {
    props!: PopperWrapperProps;

    base!: HTMLElement;

    private popper: Nullable<Instance> = null;

    componentDidMount() {
        const button = this.base.previousSibling as Element;
        const dropdown = this.base.querySelector('.ns-dropdown') as HTMLElement;
        if (button && dropdown) {
            this.popper = createPopper(button, dropdown, {
                modifiers: [
                    {
                        name: 'offset',
                        options: {
                            offset: [4, 15]
                        }
                    }
                ],
                placement: 'bottom-start'
            });
        }
    }

    componentDidUpdate() {
        if (this.popper) {
            this.popper.update();
        }
    }

    componentWillUnmount() {
        if (this.popper) {
            this.popper.destroy();
            this.popper = null;
        }
    }

    toggle = (props: PopperWrapperProps) => {
        return props.expanded ? { display: 'block' } : { display: 'none' };
    };

    render = () => <div style={this.toggle(this.props)}>{this.props.children}</div>;
}
