import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css } from 'emotion';
import { MouseEventHander } from '../type-dictionary/index';

import { optionalProp } from '../preact-common';
import { button, ellipsis } from '../design-system';

export interface TextButtonPProps {
    caption?: string;
    className?: string;
    onClick?: MouseEventHander;
    testid?: string;
}

const className = css({
    ...button,
    ...ellipsis,
    maxWidth: 150,
    minWidth: 50
});

const caption = (props: TextButtonPProps) => props.caption || 'Caption';

const testid = (props: TextButtonPProps) => optionalProp<string>('data-testid', props.testid);

const onClick = (props: TextButtonPProps) => optionalProp('onClick', props.onClick);

export const TextButtonP: FunctionComponent<TextButtonPProps> = props => (
    <button
        {...{
            className,
            ...testid(props),
            ...onClick(props)
        }}
    >
        {caption(props)}
    </button>
);
