import { css } from 'emotion';
import { StatelessDucklet, Update } from '@ix/guiduck';
import { MountPoint } from '../guiduck';

export type Attrs = undefined;

export type Type = StatelessDucklet<Attrs>;

const className = css({
    display: 'inline-block',
    height: '24px',
    width: '10px'
});

class SpaceDivider implements Type {
    private mountPoint = new MountPoint();

    mount(element: HTMLElement): this {
        this.mountPoint.mount(element, 'div');
        this.mountPoint.withFirstChild(firstChild => {
            firstChild.classList.add(className);
        });
        return this;
    }

    unmount(): HTMLElement {
        return this.mountPoint.unmount();
    }

    update(callback: Update<Attrs, undefined>): this {
        callback();
        this.preventUpdateBeforeMount();
        return this;
    }

    private preventUpdateBeforeMount(): void {
        this.mountPoint.withFirstChild(() => undefined);
    }
}

export function of(): Type {
    return new SpaceDivider();
}
