import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import * as State from '../model/state';
import * as Context from '../../context';
import { Toggle } from './toggle'; // eslint-disable-line @typescript-eslint/no-unused-vars

export interface ToggleContainerInjectedProps {
    menu__opened: () => void;
    toggleTooltip: string;
}

export function mapStateToProps(state: State.Type): ToggleContainerInjectedProps {
    const { menu__opened } = Context.actions();
    return {
        menu__opened,
        toggleTooltip: State.toggleTooltip(state)
    };
}

export const PureToggleContainer: FunctionComponent<ToggleContainerInjectedProps> = ({
    menu__opened,
    toggleTooltip
}) => <Toggle tooltip={toggleTooltip} onClick={menu__opened} />;

export const ToggleContainer = Context.connect(PureToggleContainer, mapStateToProps);
