import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css, cx } from 'emotion';
import {
    gray12,
    gray15,
    primaryDefault,
    sensibleBoxModel,
    geFont,
    clickableCursor,
    defaultCursor
} from '../../../design-system';

export interface ExpandCollapseAllProps {
    collapseLabel: string;
    expandLabel: string;
    state: ExpandedState;
    onExpandClick: () => void;
    onCollapseClick: () => void;
}

export type ExpandedState = 'all-expanded' | 'all-collapsed' | 'mixed';

const className = css({
    ...sensibleBoxModel(),
    height: '35px',
    padding: '10px 20px 10px 0',
    textAlign: 'right',
    width: '100%'
});

const defaultText = css({
    ...geFont(),
    display: 'inline-block',
    fontSize: '15px'
});

const enabledText = css({
    ...clickableCursor(),
    color: primaryDefault
});

const disabledText = css({
    ...defaultCursor(),
    color: gray12,
    cursor: 'not-allowed'
});

const pipe = css({
    ...geFont(),
    color: gray15,
    display: 'inline-block',
    fontSize: '15px',
    padding: '0 5px 0 5px'
});

export const ExpandCollapseAll: FunctionComponent<ExpandCollapseAllProps> = (props: ExpandCollapseAllProps) => (
    <div data-testid="app-menu__expand-collapse-all" className={className}>
        <span className={expandClassName(props)} title={expandTooltip(props)} onClick={onExpandClick(props)}>
            {props.expandLabel}
        </span>
        <span className={pipe}>|</span>
        <span className={collapseClassName(props)} title={collapseTooltip(props)} onClick={onCollapseClick(props)}>
            {props.collapseLabel}
        </span>
    </div>
);

function expandClassName({ state }: ExpandCollapseAllProps): string {
    return cx(defaultText, {
        [disabledText]: state === 'all-expanded',
        [enabledText]: state === 'mixed' || state === 'all-collapsed'
    });
}

function expandTooltip({ state, expandLabel }: ExpandCollapseAllProps): string | undefined {
    if (state === 'all-expanded') return undefined;
    return expandLabel;
}

function onExpandClick(props: ExpandCollapseAllProps): (() => void) | undefined {
    if (props.state === 'all-expanded') return undefined;
    return props.onExpandClick;
}

function collapseClassName({ state }: ExpandCollapseAllProps): string {
    return cx(defaultText, {
        [disabledText]: state === 'all-collapsed',
        [enabledText]: state === 'mixed' || state === 'all-expanded'
    });
}

function collapseTooltip({ state, collapseLabel }: ExpandCollapseAllProps): string | undefined {
    if (state === 'all-collapsed') return undefined;
    return collapseLabel;
}

function onCollapseClick(props: ExpandCollapseAllProps): (() => void) | undefined {
    if (props.state === 'all-collapsed') return undefined;
    return props.onCollapseClick;
}
