/* eslint-disable no-shadow */

import { AnyComponent, ComponentConstructor } from 'preact';
import * as unistorePreact from 'unistore/preact';
import * as Actions from './actions';
import * as Folder from './core/model/folder';
import * as State from './core/model/state';
import * as Store from './store';

export interface Type {
    readonly actions: BoundActions;
    readonly store: Store.Type;
}

export interface BoundActions {
    folder__added(folders: Folder.Type[]): void;
    folder__closed(id: Folder.Id): void;
    folder__opened(id: Folder.Id): void;
    folder__allOpened(): void;
    folder__allClosed(): void;
    letter__added(letters: Folder.Type[]): void;
    letter__closed(id: Folder.Id): void;
    letter__opened(id: Folder.Id): void;
    letter__allOpened(): void;
    letter__allClosed(): void;
    menu__closed(): void;
    menu__opened(): void;
    view__toggled(): void;
}

let singleton: Type | undefined;

export function of(state: State.Type): Type {
    const store = Store.of(state);
    const actions = Actions.of();
    singleton = {
        actions: {
            folder__added: store.action(actions.folder__added),
            folder__closed: store.action(actions.folder__closed),
            folder__opened: store.action(actions.folder__opened),
            folder__allOpened: store.action(actions.folder__allOpened),
            folder__allClosed: store.action(actions.folder__allClosed),
            letter__added: store.action(actions.letter__added),
            letter__closed: store.action(actions.letter__closed),
            letter__opened: store.action(actions.letter__opened),
            letter__allOpened: store.action(actions.letter__allOpened),
            letter__allClosed: store.action(actions.letter__allClosed),
            menu__closed: store.action(actions.menu__closed),
            menu__opened: store.action(actions.menu__opened),
            view__toggled: store.action(actions.view__toggled)
        },
        store
    };
    return singleton;
}

export function instance(): Type {
    if (!singleton) throw new Error('Context not created yet');
    return singleton;
}

export function store(): Store.Type {
    if (!singleton) throw new Error('Context not created yet');
    return singleton.store;
}

export function actions(): BoundActions {
    if (!singleton) throw new Error('Context not created yet');
    return singleton.actions;
}

export function connect<PassedInProps, PropsToInject>(
    component: AnyComponent<PassedInProps & PropsToInject, unknown>,
    mapStateToProps: Store.StateMapper<PassedInProps, PropsToInject>
): ComponentConstructor<PassedInProps | (PassedInProps & PropsToInject), unknown> {
    return unistorePreact.connect(mapStateToProps, {})(component);
}
