import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { Menu } from './menu'; // eslint-disable-line @typescript-eslint/no-unused-vars
import * as Context from '../../context';
import * as State from '../model/state';

export interface MenuContainerInjectedProps {
    display: State.Display;
    onClose: () => void;
    title: string;
    closeTooltip: string;
    onViewToggleClick: () => void;
    showAlphaViewTooltip: string;
    showFolderViewTooltip: string;
    applicationViewType: string;
}

export function mapStateToProps(state: State.Type): MenuContainerInjectedProps {
    const { menu__closed, view__toggled } = Context.actions();
    return {
        onClose: menu__closed,
        display: State.display(state),
        title: State.title(state),
        closeTooltip: State.closeTooltip(state),
        onViewToggleClick: view__toggled,
        showAlphaViewTooltip: State.showAlphaViewTooltip(state),
        showFolderViewTooltip: State.showFolderViewTooltip(state),
        applicationViewType: State.applicationViewType(state)
    };
}

export const PureMenuContainer: FunctionComponent<MenuContainerInjectedProps> = ({
    display,
    onClose,
    title,
    closeTooltip,
    onViewToggleClick,
    showAlphaViewTooltip,
    showFolderViewTooltip,
    applicationViewType
}) => (
    <Menu
        display={display}
        onClose={onClose}
        title={title}
        closeTooltip={closeTooltip}
        onViewToggleClick={onViewToggleClick}
        showAlphaViewTooltip={showAlphaViewTooltip}
        applicationViewType={applicationViewType}
        showFolderViewTooltip={showFolderViewTooltip}
    />
);

export const MenuContainer = Context.connect(PureMenuContainer, mapStateToProps);
