const removeQueryString = (helpContext: string | undefined) => {
    const hasQueryString = helpContext?.indexOf('%3F') !== -1;
    if (hasQueryString) {
        return helpContext?.substring(0, helpContext.indexOf('%3F'));
    }
    return helpContext;
};

const removeRelativePathing = (context: string | undefined) => {
    return context?.split('../').pop() || context;
};

export const formatContextForRedirect = (context: string | undefined) => {
    let helpContext = context;
    helpContext = removeRelativePathing(helpContext);
    helpContext = removeQueryString(helpContext);
    return helpContext;
};
