/* eslint-disable @typescript-eslint/camelcase */

import { css } from 'emotion';
import { Ducklet } from '@ix/guiduck';
import { tabBar, utilityMenu, utilityMenu__icon, utilityMenu__target, px } from './design-system';
import { ImmutableDuckletList } from './ui-primitives';

export type Attrs = ImmutableDuckletList.Attrs;

export type Type = ImmutableDuckletList.Type;

const target = {
    between__px: utilityMenu__icon.between__px - (utilityMenu__target.size__px - utilityMenu__icon.size__px)
};

const menuItem = {
    paddingHorizontal__px: target.between__px / 2,
    paddingVertical__px: 0
};

const menu = {
    ...utilityMenu,
    paddingLeft__px: 0,
    paddingRight__px: utilityMenu.fromEdge__px - menuItem.paddingHorizontal__px,
    paddingVertical__px: (tabBar.height__px - utilityMenu__target.size__px) / 2
};

const utilityMenuClassName = css({
    display: 'inline-block',
    paddingBottom: px(menu.paddingVertical__px),
    paddingLeft: px(menu.paddingLeft__px),
    paddingRight: px(menu.paddingRight__px),
    paddingTop: px(menu.paddingVertical__px),

    li: {
        display: 'inline-block',
        paddingBottom: px(menuItem.paddingVertical__px),
        paddingLeft: px(menuItem.paddingHorizontal__px),
        paddingRight: px(menuItem.paddingHorizontal__px),
        paddingTop: px(menuItem.paddingVertical__px)
    }
});

export function of(children: Ducklet<any, any>[], attrs: Partial<Attrs> = {}): Type {
    return ImmutableDuckletList.of(children, utilityMenuClassName, attrs);
}
