import produce, { enableES5 } from 'immer';
import * as App from './app';
import * as Folder from './folder';
import * as IdIndex from './id-index';

enableES5();

export interface Type {
    readonly entities: Entities;
    readonly ui: UserInterface;
}

export interface Entities {
    readonly folders: Folders;
    readonly letters: Folders;
}

export type Folders = {
    readonly [key: string]: Folder.Type;
};

const DEFAULT_ENTITIES: Entities = {
    folders: {},
    letters: {}
};

export interface UserInterface {
    readonly display: Display;
    readonly folderOrder: IdIndex.Type<Folder.Id>;
    readonly openFolders: IdIndex.Type<Folder.Id>;
    readonly letterOrder: IdIndex.Type<Folder.Id>;
    readonly openLetters: IdIndex.Type<Folder.Id>;
    readonly title: string;
    readonly toggleTooltip: string;
    readonly expandAllLabel: string;
    readonly collapseAllLabel: string;
    readonly closeTooltip: string;
    readonly showAlphaViewTooltip: string;
    readonly showFolderViewTooltip: string;
    readonly applicationViewType: ApplicationViewType;
}

const OPENED = 'opened';
const CLOSED = 'closed';

export type Display = typeof OPENED | typeof CLOSED;

const FOLDERS = 'folders';
const ALPHABETICAL = 'alphabetical';

export type ApplicationViewType = typeof FOLDERS | typeof ALPHABETICAL;

const DEFAULT_UI: UserInterface = {
    display: CLOSED,
    folderOrder: [],
    openFolders: [],
    letterOrder: [],
    openLetters: [],
    title: '',
    toggleTooltip: '',
    expandAllLabel: '',
    collapseAllLabel: '',
    closeTooltip: '',
    showAlphaViewTooltip: '',
    showFolderViewTooltip: '',
    applicationViewType: FOLDERS
};

export function of(entities: Partial<Entities> = {}, ui: Partial<UserInterface> = {}): Type {
    return {
        entities: {
            ...DEFAULT_ENTITIES,
            ...entities
        },
        ui: {
            ...DEFAULT_UI,
            ...ui
        }
    };
}

export function open(state: Type): Type {
    if (state.ui.display === OPENED) return state;
    return produce(state, draft => {
        draft.ui.display = OPENED;
    });
}

export function close(state: Type): Type {
    if (state.ui.display === CLOSED) return state;
    return produce(state, draft => {
        draft.ui.display = CLOSED;
    });
}

export function applicationViewType(state: Type): ApplicationViewType {
    return state.ui.applicationViewType;
}

export function display(state: Type): Display {
    return state.ui.display;
}

export function title(state: Type): string {
    return state.ui.title;
}

export function closeTooltip(state: Type): string {
    return state.ui.closeTooltip;
}

export function openFolders(state: Type): IdIndex.Type<Folder.Id> {
    return state.ui.openFolders;
}

export function openLetters(state: Type): IdIndex.Type<Folder.Id> {
    return state.ui.openLetters;
}

export function folders(state: Type): Folders {
    return state.entities.folders;
}

export function letters(state: Type): Folders {
    // big ugly calculation to figure out what letters are available
    return state.entities.letters;
}

export function folderById(state: Type, id: Folder.Id): Folder.Type {
    return state.entities.folders[id];
}

export function letterById(state: Type, id: Folder.Id): Folder.Type {
    return state.entities.letters[id];
}

export function openFolder(state: Type, id: Folder.Id): Type {
    if (isFolderOpen(state, id)) return state;
    return produce(state, draft => {
        const index = openFolders(state);
        draft.ui.openFolders = IdIndex.add(index, id);
    });
}

export function isFolderOpen(state: Type, id: Folder.Id): boolean {
    return IdIndex.includes(state.ui.openFolders, id);
}

export function closeFolder(state: Type, id: Folder.Id): Type {
    if (!isFolderOpen(state, id)) return state;
    return produce(state, draft => {
        const index = openFolders(state);
        draft.ui.openFolders = IdIndex.remove(index, id);
    });
}

export function openLetter(state: Type, id: Folder.Id): Type {
    if (isLetterOpen(state, id)) return state;
    return produce(state, draft => {
        const index = openLetters(state);
        draft.ui.openLetters = IdIndex.add(index, id);
    });
}

export function isLetterOpen(state: Type, id: Folder.Id): boolean {
    return IdIndex.includes(state.ui.openLetters, id);
}

export function closeLetter(state: Type, id: Folder.Id): Type {
    if (!isLetterOpen(state, id)) return state;
    return produce(state, draft => {
        const index = openLetters(state);
        draft.ui.openLetters = IdIndex.remove(index, id);
    });
}

export function folderOrder(state: Type): IdIndex.Type<Folder.Id> {
    return state.ui.folderOrder;
}

export function letterOrder(state: Type): IdIndex.Type<Folder.Id> {
    return state.ui.letterOrder;
}

export function appsByFolderId(state: Type, id: Folder.Id): App.Type[] {
    const folder = folderById(state, id);
    return Folder.apps(folder);
}

export function appsByLetterId(state: Type, id: Folder.Id): App.Type[] {
    const letter = letterById(state, id);
    return Folder.apps(letter);
}

export function addFolders(state: Type, newFolders: Folder.Type[]): Type {
    return produce(state, draft => {
        newFolders.forEach(folder => {
            draft.entities.folders[Folder.id(folder)] = folder;
            (draft.ui.folderOrder as Folder.Id[]).push(folder.id);
            (draft.ui.openFolders as Folder.Id[]).push(folder.id);
        });
    });
}

export function addLetters(state: Type, newLetters: Folder.Type[]): Type {
    return produce(state, draft => {
        const flatAppList = flattenAppList(newLetters);
        const letterList = buildLetterList(flatAppList);
        letterList.forEach(letter => {
            draft.entities.letters[Folder.id(letter)] = letter;
            (draft.ui.letterOrder as Folder.Id[]).push(letter.id);
            (draft.ui.openLetters as Folder.Id[]).push(letter.id);
        });
    });
}

export function toggleTooltip(state: Type): string {
    return state.ui.toggleTooltip;
}

export function showAlphaViewTooltip(state: Type): string {
    return state.ui.showAlphaViewTooltip;
}

export function showFolderViewTooltip(state: Type): string {
    return state.ui.showFolderViewTooltip;
}

export function toggleView(state: Type): Type {
    return produce(state, draft => {
        if (state.ui.applicationViewType === FOLDERS) {
            draft.ui.applicationViewType = ALPHABETICAL;
        }
        if (state.ui.applicationViewType === ALPHABETICAL) {
            draft.ui.applicationViewType = FOLDERS;
        }
    });
}

function buildAlphabetFromApps(flatAppList: App.Type[]): any {
    const lettersWithApps: any = {};
    flatAppList.forEach(app => {
        const name = app.label || app.labelKey;
        const firstLetter = name.charAt(0).toUpperCase();
        lettersWithApps[firstLetter] = firstLetter;
    });
    return alphabetizeStrings(Object.keys(lettersWithApps));
}

function alphabetizeStrings(strings: string[]): string[] {
    return strings?.sort((a: string, b: string) => {
        if (a < b) {
            return -1;
        }
        if (a > b) {
            return 1;
        }
        return 0;
    });
}

function buildAppsForLetter(letter: string, flatAppList: Array<App.Type>): App.Type[] {
    const apps: App.Type[] = [];
    flatAppList.forEach((app: App.Type) => {
        if (appStartsWith(app, letter)) {
            apps.push(app);
        }
    });
    return apps;
}

function alphabetizeAppsForLetter(apps: App.Type[]): App.Type[] {
    return apps.sort((a: App.Type, b: App.Type) => {
        if (a.label?.toUpperCase() < b.label?.toUpperCase()) {
            return -1;
        }
        if (a.label?.toUpperCase() > b.label?.toUpperCase()) {
            return 1;
        }
        return 0;
    });
}

function buildLetterList(flatAppList: Array<App.Type>): Folder.Type[] {
    const alphabet = buildAlphabetFromApps(flatAppList);

    return alphabet.map((letter: string) => {
        let apps = buildAppsForLetter(letter, flatAppList);
        apps = alphabetizeAppsForLetter(apps);
        return Folder.of(letter, letter, letter, letter, apps);
    });
}

function appStartsWith(app: App.Type, letter: string) {
    return app.label?.toUpperCase().indexOf(letter, 0) === 0;
}

function flattenAppList(newFolders: Folder.Type[]): Array<App.Type> {
    const flatAppList: Array<App.Type> = [];
    newFolders.forEach(folder => {
        folder.apps.forEach(app => {
            if (app.subApps.length) {
                app.subApps.forEach(subApp =>
                    flatAppList.push(
                        App.of(
                            subApp.id,
                            subApp.href,
                            subApp.labelKey,
                            subApp.label,
                            [],
                            `${folder.label || folder.labelKey} > ${app.label || app.labelKey}`
                        )
                    )
                );
            }
            if (app.href) {
                // an app without an href does not belong in a flat list
                flatAppList.push(
                    App.of(app.id, app.href, app.labelKey, app.label, [], `${folder.label || folder.labelKey}`)
                );
            }
        });
    });
    return flatAppList;
}

export function openAllFolders(state: Type): Type {
    return produce(state, draft => {
        const index = openFolders(state);
        draft.ui.openFolders = IdIndex.add(index, ...state.ui.folderOrder);
    });
}

export function closeAllFolders(state: Type): Type {
    return produce(state, draft => {
        const index = openFolders(state);
        draft.ui.openFolders = IdIndex.empty(index);
    });
}

export function openAllLetters(state: Type): Type {
    return produce(state, draft => {
        const index = openLetters(state);
        draft.ui.openLetters = IdIndex.add(index, ...state.ui.letterOrder);
    });
}

export function closeAllLetters(state: Type): Type {
    return produce(state, draft => {
        const index = openLetters(state);
        draft.ui.openLetters = IdIndex.empty(index);
    });
}

export function expandAllLabel(state: Type): string {
    return state.ui.expandAllLabel;
}

export function collapseAllLabel(state: Type): string {
    return state.ui.collapseAllLabel;
}
