import { Nullable } from '../type-dictionary';

export class MountPoint {
    private static assertHtmlChild(child: Nullable<ChildNode>): asserts child is HTMLElement {
        if (!child) throw new Error('Child node does not exist');
        if (child.nodeType !== Node.ELEMENT_NODE) throw new Error('Child node is not a HTMLElement');
    }

    private static assertMounted(element: Nullable<HTMLElement>): asserts element {
        if (!element) throw new Error('Component has not been mounted');
    }

    private element: Nullable<HTMLElement> = null;

    withFirstChild(callback: (element: HTMLElement) => void): void {
        MountPoint.assertMounted(this.element);
        const { firstChild } = this.element;
        MountPoint.assertHtmlChild(firstChild);
        callback(firstChild);
    }

    mount(element: HTMLElement, tag: string): void {
        if (this.element) throw new Error('Component is already mounted');
        const firstChild = document.createElement(tag);
        this.element = element;
        this.element.appendChild(firstChild);
    }

    unmount(): HTMLElement {
        this.clearContents();
        return this.removeElementReference();
    }

    private clearContents() {
        MountPoint.assertMounted(this.element);
        while (this.element.firstChild) {
            this.element.removeChild(this.element.firstChild);
        }
    }

    private removeElementReference(): HTMLElement {
        MountPoint.assertMounted(this.element);
        const { element } = this;
        this.element = null;
        return element;
    }
}
