/* eslint-disable no-shadow */

import * as App from './app';

export interface Type {
    id: Id;
    name: string;
    label: string;
    labelKey: string;
    apps: App.Type[];
}

export type Id = string;

export function of(id: Id, name: string, labelKey: string, label: string, apps: App.Type[]): Type {
    return Object.freeze({ id, name, labelKey, label, apps });
}

export const NOTHING = of('', '', '', '', []);

export function ofNothing(): Type {
    return NOTHING;
}

export function id(folder: Type): Id {
    return folder.id;
}

export function name(folder: Type): string {
    return folder.name;
}

export function label(folder: Type): string {
    return folder.label;
}

export function labelKey(folder: Type): string {
    return folder.labelKey;
}

export function apps(folder: Type): App.Type[] {
    return folder.apps;
}
