import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { ExpandCollapseAll, ExpandCollapseAllProps, ExpandedState } from './expand-collapse-all'; // eslint-disable-line @typescript-eslint/no-unused-vars
import * as Context from '../../context';
import * as State from '../../core/model/state';

export function mapStateToProps(state: State.Type): ExpandCollapseAllProps {
    const { folder__allClosed, folder__allOpened, letter__allClosed, letter__allOpened } = Context.actions();
    let onCollapseClick: any;
    let onExpandClick: any;
    if (State.applicationViewType(state) === 'folders') {
        onCollapseClick = folder__allClosed;
        onExpandClick = folder__allOpened;
    }
    if (State.applicationViewType(state) === 'alphabetical') {
        onCollapseClick = letter__allClosed;
        onExpandClick = letter__allOpened;
    }
    return {
        collapseLabel: State.collapseAllLabel(state),
        expandLabel: State.expandAllLabel(state),
        state: toState(state),
        onCollapseClick,
        onExpandClick
    };
}

function toState(state: State.Type): ExpandedState {
    if (State.applicationViewType(state) === 'folders') {
        return toStateWithFolders(state);
    }
    if (State.applicationViewType(state) === 'alphabetical') {
        return toStateWithLetters(state);
    }
    return 'all-expanded';
}

function toStateWithFolders(state: State.Type): ExpandedState {
    const openFolderCount = State.openFolders(state).length;
    const allFolderCount = State.folderOrder(state).length;
    if (openFolderCount === 0) return 'all-collapsed';
    if (openFolderCount === allFolderCount) return 'all-expanded';
    return 'mixed';
}

function toStateWithLetters(state: State.Type): ExpandedState {
    const openLetterCount = State.openLetters(state).length;
    const allLetterCount = State.letterOrder(state).length;
    if (openLetterCount === 0) return 'all-collapsed';
    if (openLetterCount === allLetterCount) return 'all-expanded';
    return 'mixed';
}

export const PureExpandCollapseAllContainer: FunctionComponent<ExpandCollapseAllProps> = props => (
    <ExpandCollapseAll
        collapseLabel={props.collapseLabel}
        expandLabel={props.expandLabel}
        onCollapseClick={props.onCollapseClick}
        onExpandClick={props.onExpandClick}
        state={props.state}
    />
);

export const ExpandCollapseAllContainer = Context.connect(PureExpandCollapseAllContainer, mapStateToProps);
