import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css } from 'emotion';
import { gray3, gray18 } from '../../../design-system';
import { ApplicationProps, Application } from './application'; // eslint-disable-line @typescript-eslint/no-unused-vars

export interface FolderProps {
    label: string;
    labelKey?: string;
    tooltip: string;
    apps: Array<ApplicationProps>;
    expanded?: boolean;
    onClick: (ev: any) => void;
    viewType: string;
}

const FolderComponentStyle = css`
    border-top: 1px solid transparent;
`;

const FolderButtonStyle = css`
    font-size: 15px;
    font-family: 'GE Inspira Sans';
    color: ${gray18};
    background-color: ${gray3};
    height: 30px;
    margin: 0;
    padding: 0;
    cursor: pointer;
    line-height: 30px;

    i {
        line-height: 30px;
        vertical-align: middle;
        padding-left: 10px;
        padding-right: 10px;
    }
`;

const ulStyle = css`
    padding: 0;
    margin: 0;
`;

const icon = (props: FolderProps) => (props.expanded ? 'gedi gedi-chevron-down' : 'gedi gedi-chevron-right');

const folderTestId = (props: FolderProps) => {
    return `app-menu__folder::${props.labelKey || props.label}`;
};

const folderOpenTestId = (props: FolderProps) => {
    return `app-menu__folder--expanded::${props.labelKey || props.label}`;
};

const showApps = (props: FolderProps) => {
    return props.expanded ? { display: 'block' } : { display: 'none' };
};

const apps = (props: FolderProps) => {
    return props.apps.map(app => (
        <Application
            label={app.label}
            labelKey={app.labelKey}
            folderLabel={props.label}
            folderKey={props.labelKey}
            tooltip={app.tooltip}
            href={app.href}
            subApps={app.subApps}
            target={app.target}
            viewType={props.viewType}
            subscript={app.subscript}
        />
    ));
};

export const Folder: FunctionComponent<FolderProps> = props => (
    <div className={FolderComponentStyle}>
        <div
            className={FolderButtonStyle}
            title={props.tooltip}
            data-testid={folderTestId(props)}
            onClick={props.onClick}
        >
            <i className={icon(props)} />
            {props.label.toUpperCase()}
        </div>
        <ul style={showApps(props)} className={ulStyle} data-testid={folderOpenTestId(props)}>
            {apps(props)}
        </ul>
    </div>
);
