import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { Folder, FolderProps } from './folder'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { ApplicationProps } from './application'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { mapApplicationProps } from './map-application-props';
import * as Context from '../../context';
import * as FolderModel from '../../core/model/folder';
import * as State from '../../core/model/state';

export interface FolderContainerProps {
    id: FolderModel.Id;
    viewType: string;
}

export function mapStateToProps(state: State.Type, { id, viewType }: FolderContainerProps): FolderProps {
    let folder = FolderModel.ofNothing();
    let expanded = false;
    let apps: ApplicationProps[] = [];

    if (viewType === 'folders') {
        folder = State.folderById(state, id);
        expanded = State.isFolderOpen(state, id);
        apps = State.appsByFolderId(state, id).map(app => mapApplicationProps(folder, app));
    }
    if (viewType === 'alphabetical') {
        folder = State.letterById(state, id);
        expanded = State.isLetterOpen(state, id);
        apps = State.appsByLetterId(state, id).map(app => mapApplicationProps(folder, app));
    }
    return {
        expanded,
        onClick: onClick(id, expanded, viewType),
        label: FolderModel.label(folder),
        labelKey: FolderModel.labelKey(folder),
        tooltip: FolderModel.label(folder),
        apps,
        viewType
    };
}

function onClick(id: FolderModel.Id, expanded: boolean, viewType: string): () => void {
    const { folder__opened, folder__closed, letter__opened, letter__closed } = Context.actions();
    if (viewType === 'alphabetical') {
        return () => (expanded ? letter__closed(id) : letter__opened(id));
    }
    return () => (expanded ? folder__closed(id) : folder__opened(id));
}

export const PureFolderContainer: FunctionComponent<FolderProps> = props => (
    <Folder
        label={props.label}
        labelKey={props.labelKey}
        tooltip={props.tooltip}
        expanded={props.expanded}
        onClick={props.onClick}
        apps={props.apps}
        viewType={props.viewType}
    />
);

export const FolderContainer = Context.connect(PureFolderContainer, mapStateToProps);
