import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css, cx } from 'emotion';
import { dropdown } from '@ix/design-tokens/lib/dropdown';
import { optionalProp, StatelessEvent } from '../preact-common';

export interface DropdownItemProps {
    onClick: (ev: DropdownItemClickEvent) => void;
    testid?: string;
    icon?: string;
    topSeparator?: boolean;
    bottomSeparator?: boolean;
    caption: string;
}

export type DropdownItemClickEvent = StatelessEvent<MouseEvent, DropdownItemProps>;

const DropdownItemStyle = css`
    -webkit-user-select: none; /* Chrome/Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+ */
    -o-user-select: none;
    user-select: none;

    cursor: pointer;
    height: ${dropdown.panel.item.height};
    background-color: ${dropdown.panel.item.normal.bgColor.light};
    color: ${dropdown.panel.item.normal.fgColor.light};
    font-size: ${dropdown.panel.item.fontSize};
    text-align: left;

    :hover :active {
        background-color: ${dropdown.panel.item.selected.bgColor.light};
        color: ${dropdown.panel.item.selected.fgColor.light};
    }
    :hover {
        background-color: ${dropdown.panel.item.hover.bgColor.light};
    }

    a {
        align-items: center;
        color: ${dropdown.panel.item.normal.fgColor.light};
        display: flex;
        height: 100%;
        line-height: ${dropdown.panel.item.height};
        padding-right: ${dropdown.panel.padding};
        text-decoration: none;
    }

    a:hover {
        color: ${dropdown.panel.item.normal.fgColor.light};
        text-decoration: none;
    }
`;

const checkForSeparators = (props: DropdownItemProps) => {
    if (props.topSeparator && props.bottomSeparator) {
        return `
            border-top: ${dropdown.panel.border.light};
            border-bottom: ${dropdown.panel.border.light};
        `;
    }
    if (props.bottomSeparator) {
        return `
            border-bottom: ${dropdown.panel.border.light};
        `;
    }
    if (props.topSeparator) {
        return `
            border-top: ${dropdown.panel.border.light};
        `;
    }
    return '';
};

const iconStyle = (props: DropdownItemProps) => {
    if (props.icon) {
        return css`
            padding-left: ${dropdown.panel.padding};
            padding-right: ${dropdown.panel.padding};
        `;
    }
    return css`
        padding-left: ${dropdown.panel.padding};
    `;
};

const onClickWrapper = (props: DropdownItemProps, event: MouseEvent) => {
    const customEvent = {
        props,
        nativeEvent: event
    };
    props.onClick(customEvent);
};
const testid = (props: DropdownItemProps) => optionalProp<string>('data-testid', props.testid);
const onClick = (props: DropdownItemProps) =>
    optionalProp<(ev: MouseEvent) => void>('onClick', onClickWrapper.bind(null, props));

export const DropdownItem: FunctionComponent<DropdownItemProps> = props => (
    <div className={DropdownItemStyle} style={checkForSeparators(props)} {...{ ...onClick(props) }}>
        <a className={'ns-dropdown-item'} {...{ ...testid(props) }}>
            <i className={cx(props.icon, iconStyle(props))} />
            {props.caption}
        </a>
    </div>
);
