import { ApplicationProps } from './application';
import { mapSubAppProps } from './map-subapp-props';
import * as App from '../../core/model/app';
import * as Folder from '../../core/model/folder';

export function mapApplicationProps(folder: Folder.Type, app: App.Type): ApplicationProps {
    return {
        label: App.label(app),
        labelKey: App.labelkey(app),
        folderLabel: Folder.label(folder),
        folderKey: Folder.labelKey(folder),
        tooltip: App.label(app),
        href: App.href(app),
        subApps: App.subApps(app).map(subApp => mapSubAppProps(folder, subApp)),
        target: 'tab',
        subscript: App.subscript(app)
    };
}
