export type Type<T> = ReadonlyArray<T>;

export function of<T>(contents: T[] = []): Type<T> {
    return contents;
}

export function length<T>(index: Type<T>): number {
    return index.length;
}

export function add<T>(index: Type<T>, ...ids: T[]): Type<T> {
    const toAdd = ids.filter(id => !includes(index, id));
    if (toAdd.length === 0) return index;
    return [...index, ...toAdd];
}

export function includes<T>(index: Type<T>, id: T): boolean {
    return index.indexOf(id) > -1;
}

export function remove<T>(index: Type<T>, id: T): Type<T> {
    if (!includes(index, id)) return index;
    return index.filter(item => item !== id);
}

export function empty<T>(index: Type<T>): Type<T> {
    if (length(index) === 0) return index;
    return of([]);
}
