import { h, Component } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css, cx } from 'emotion';
import { dropdown } from '@ix/design-tokens/lib/dropdown';
import { PopperWrapper } from './popper-wrapper'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { DropdownItem, DropdownItemProps } from './dropdown-item'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { optionalProp, requiredProp } from '../preact-common';
import { TransparentButton } from '../common';

export interface DropdownProps {
    onClick?: (ev: any) => void;
    tooltip: string;
    testid?: string;
    expanded?: boolean;
    children: Array<DropdownItemProps>;
    icon: string;
    dropdownTestId?: string;
}

const DropdownStyle = css`
    border: ${dropdown.panel.border.light};
    box-shadow: ${dropdown.panel.shadow.light};
    width: max-content;
    font-family: 'GE Inspira Sans';
`;

const ButtonStyle = cx(
    TransparentButton,
    css({
        height: '22px',
        lineHeight: 0,
        margin: 0,
        padding: 0,
        width: '22px',

        i: {
            fontSize: '16px'
        }
    })
);

export class Dropdown extends Component {
    base!: HTMLElement;

    state: { expanded?: boolean };

    props!: DropdownProps;

    setState: any;

    windowClickHandler!: any;

    constructor(props: DropdownProps) {
        super(props);
        this.state = {
            expanded: props.expanded || false
        };
    }

    componentDidMount() {
        this.windowClickHandler = this.closeDropdownOnClick.bind(this);
        window.addEventListener('click', this.windowClickHandler);
    }

    closeDropdownOnClick(event: any) {
        if (!this.base?.contains(event.target) || event?.target?.classList.contains('ns-dropdown-item')) {
            this.setState({ expanded: false });
        }
    }

    componentWillUnmount() {
        window.removeEventListener('click', this.windowClickHandler);
        this.windowClickHandler = null;
    }

    testid = (props: DropdownProps) => optionalProp<string>('data-testid', props.testid);

    dropdownTestId = (props: DropdownProps) => optionalProp<string>('data-testid', props.dropdownTestId);

    onClickWrapper = () => {
        this.setState({ expanded: !this.state.expanded });
    };

    tooltip = (props: DropdownProps) => requiredProp<string>('title', props.tooltip);

    icon = (props: DropdownProps) => requiredProp<string>('className', props.icon);

    onClick = (props: DropdownProps) =>
        optionalProp<(ev: MouseEvent) => void>('onClick', this.onClickWrapper.bind(null, props));

    expanded = (props: DropdownProps) => optionalProp('expanded', props.expanded);

    dropdownItems = (props: DropdownProps) =>
        props.children.map((dropdownItem: DropdownItemProps) => (
            <DropdownItem
                caption={dropdownItem.caption}
                icon={dropdownItem.icon}
                onClick={dropdownItem.onClick}
                topSeparator={dropdownItem.topSeparator}
                bottomSeparator={dropdownItem.bottomSeparator}
                testid={dropdownItem.testid}
            />
        ));

    children = (props: DropdownProps) =>
        requiredProp(
            'children',
            <div className={cx(DropdownStyle, 'ns-dropdown')} {...this.dropdownTestId(this.props)}>
                {this.dropdownItems(props)}
            </div>
        );

    render = () => (
        <div>
            <button
                className={ButtonStyle}
                {...{ ...this.onClick(this.props), ...this.testid(this.props), ...this.tooltip(this.props) }}
            >
                <i {...{ ...this.icon(this.props) }} />
            </button>
            <PopperWrapper expanded={this.state.expanded} {...{ ...this.children(this.props) }} />
        </div>
    );
}
