import { Ducklet, Update } from '@ix/guiduck';
import { Nullable } from '../type-dictionary';

export type Type<Tattrs, Tapi> = Ducklet<Tattrs, Tapi>;

class PreexistingAttrDecorator<Tattrs, Tapi> implements Type<Tattrs, Tapi> {
    private attrs: Nullable<Partial<Tattrs>>;

    private ducklet: Ducklet<Tattrs, Tapi>;

    constructor(ducklet: Ducklet<Tattrs, Tapi>, attrs: Partial<Tattrs>) {
        this.ducklet = ducklet;
        this.attrs = attrs;
    }

    mount(element: HTMLElement, attrs?: Partial<Tattrs>): this {
        const useAttrs = { ...this.attrs, ...attrs };
        this.ducklet.mount(element, useAttrs);
        this.attrs = null;
        return this;
    }

    unmount(): HTMLElement {
        return this.ducklet.unmount();
    }

    update(callback: Update<Tattrs, Tapi>): this {
        this.ducklet.update(callback);
        return this;
    }
}

export function of<Tattrs, Tapi>(ducklet: Ducklet<Tattrs, Tapi>, attrs: Partial<Tattrs>): Type<Tattrs, Tapi> {
    return new PreexistingAttrDecorator(ducklet, attrs);
}
