import { h, VNode, ComponentChildren } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { Properties } from 'csstype'; // eslint-disable-line import/no-unresolved
import { css } from 'emotion';
import * as State from '../model/state';
import { Applications } from './applications'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { gray1, gray18, absolutePosition, clickableCursor, geFont, sensibleBoxModel } from '../../../design-system';
import { ViewToggle } from './view-toggle'; // eslint-disable-line @typescript-eslint/no-unused-vars

export interface MenuProps {
    display: State.Display;
    onClose: () => void;
    title: string;
    closeTooltip: string;
    onViewToggleClick: () => void;
    showAlphaViewTooltip: string;
    showFolderViewTooltip: string;
    applicationViewType: string;
}

const ARIZONA_LEFT_NAV_Z_INDEX = 195;

const defaults: Properties = {
    ...absolutePosition(0, 0),
    ...fullWidthOnMobileButFixedOnDesktop(),
    ...sensibleBoxModel(),
    backgroundColor: gray1,
    flexDirection: 'column',
    height: '100%',
    zIndex: ARIZONA_LEFT_NAV_Z_INDEX + 5,
    boxShadow: '1px 0 3px rgba(0,0,0,0.20)'
};

function fullWidthOnMobileButFixedOnDesktop(): Properties {
    return {
        maxWidth: '480px',
        width: '100%'
    };
}

const closed = css({
    ...defaults,
    display: 'none'
});

const opened = css({
    ...defaults,
    display: 'flex'
});

const headingStyle = css({
    padding: '20px 20px 10px 20px'
});

const appTitleStyle = css({
    ...geFont(),
    fontSize: '20px',
    color: gray18
});

const appCloseButtonStyle = css({
    ...clickableCursor(),
    float: 'right',
    border: 'none',
    backgroundColor: 'transparent',
    color: gray18,
    height: '22px',
    width: '22px',
    ':focus': {
        outline: 'none'
    },
    i: {
        fontSize: '16px'
    }
});

export function Menu({
    display,
    onClose,
    title,
    closeTooltip,
    onViewToggleClick,
    showAlphaViewTooltip,
    showFolderViewTooltip,
    applicationViewType
}: MenuProps): VNode {
    return (
        <Container display={display}>
            <Heading>
                <Title title={title} />
                <CloseButton tooltip={closeTooltip} onClick={onClose} />
            </Heading>
            <ViewToggle
                showAlphaViewTooltip={showAlphaViewTooltip}
                showFolderViewTooltip={showFolderViewTooltip}
                onViewToggleClick={onViewToggleClick}
                applicationViewType={applicationViewType}
            />
            <Applications />
        </Container>
    );
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
function Container({ display, children }: { display: State.Display; children: ComponentChildren }): VNode {
    return (
        <div data-testid="app-menu" className={display === 'opened' ? opened : closed}>
            {children}
        </div>
    );
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
function Heading({ children }: { children: ComponentChildren }): VNode {
    return <div className={headingStyle}>{children}</div>;
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
function Title({ title }: { title: string }): VNode {
    return (
        <span data-testid="app-menu__title" className={appTitleStyle}>
            {title}
        </span>
    );
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
function CloseButton({ onClick, tooltip }: { tooltip: string; onClick: () => void }): VNode {
    return (
        <button data-testid="app-menu__close" className={appCloseButtonStyle} onClick={onClick} title={tooltip}>
            <i className="gedi-close"></i>
        </button>
    );
}
