import { h, FunctionComponent } from 'preact'; // eslint-disable-line @typescript-eslint/no-unused-vars
import { css } from 'emotion';
import { clickableCursor, gray18 } from '../../../design-system';

export interface ViewToggleProps {
    onViewToggleClick: () => void;
    showAlphaViewTooltip: string;
    showFolderViewTooltip: string;
    applicationViewType: string;
}

const className = css({
    width: '100%',
    paddingRight: '20px;',
    boxSizing: 'border-box'
});

const viewToggleButtonStyle = css({
    ...clickableCursor(),
    float: 'right',
    border: 'none',
    backgroundColor: 'transparent',
    color: gray18,
    height: '22px',
    width: '22px',
    ':focus': {
        outline: 'none'
    },
    i: {
        fontSize: '16px'
    }
});

const icon = (applicationViewType: string): string => {
    if (applicationViewType === 'folders') {
        return 'gedi-alphabet-sort';
    }
    if (applicationViewType === 'alphabetical') {
        return 'gedi-view-list';
    }
    return '';
};

const tooltip = (applicationViewType: string, showAlphaViewTooltip: string, showFolderViewTooltip: string): string => {
    if (applicationViewType === 'folders') {
        return showAlphaViewTooltip;
    }
    if (applicationViewType === 'alphabetical') {
        return showFolderViewTooltip;
    }
    throw new Error('applicationViewType must be "alphabetical" or "folder"');
};

// todo:  the top level div will need to be moved out to another component that will contain the view toggle and search
export const ViewToggle: FunctionComponent<ViewToggleProps> = ({
    onViewToggleClick,
    showAlphaViewTooltip,
    showFolderViewTooltip,
    applicationViewType
}) => (
    <div className={className}>
        <button
            data-testid="app-menu__view-toggle"
            title={tooltip(applicationViewType, showAlphaViewTooltip, showFolderViewTooltip)}
            className={viewToggleButtonStyle}
            onClick={onViewToggleClick}
        >
            <i className={icon(applicationViewType)} />
        </button>
    </div>
);
